import {orm} from './models';
import {createSelector} from 'reselect';
import {createSelector as ormCreateSelector} from 'redux-orm';

export const ormSelector = state => state.orm;


const tickSelector = (state) => state.tick

const getSlowTick = createSelector (
  tickSelector,
  tick=> Math.floor((tick/16)%8)
)

const getFastTick = createSelector (
  tickSelector,
  tick=> (tick%8)
)


export const aaactiveCounts = createSelector(ormSelector, getSlowTick, getFastTick, ormCreateSelector(orm, (session,slowTick, fastTick) => {
    return session.Count.all().toModelArray().filter(count=>count.position===(slowTick|| fastTick))}));



export const counts = createSelector(ormSelector, ormCreateSelector(orm, session => {
    return session.Count.all().toModelArray();
}));


export const getInstruments = createSelector(ormSelector, ormCreateSelector(orm, session => {
  console.log(session.Instrument)
    return session.Instrument;
}));



export const getCountIds = createSelector(ormSelector, ormCreateSelector(orm, session => {
    return session.Count.all().toRefArray();
}));


export const activeCountsc = createSelector(ormSelector, state => state.tick, ormCreateSelector(orm, (session, tick) => {
return session.Count.all().toModelArray().map(count => {
    const activePads = count.pads.filter({active: true}).toRefArray()
    const obj = Object.assign({}, count.ref);
    obj.duration = count.instrument.duration;
    obj.barLength = count.instrument.barLength;
    obj.pads = activePads;

    return obj

}).filter(count => ((((tick / count.duration) % count.barLength) === count.position)));
}));

export const activeCounts = createSelector(ormSelector, state => state.tick, ormCreateSelector(orm, (session, tick) => {
return session.Count.all().toModelArray().map(count => {
    const activePads = count.pads.filter({active: true}).toRefArray()
    const obj = Object.assign({}, count.ref);
    obj.duration = count.instrument.duration;
    obj.barLength = count.instrument.barLength;
    obj.pads = activePads;

    return obj

}).filter(count => ((((tick / count.duration) % count.barLength) === count.position)));
}));

export const pads = createSelector(ormSelector, ormCreateSelector(orm, session => {
  const pads = session.Pad.all().toRefArray();
return pads;
}));

export const activePads = createSelector(ormSelector, pads, ormCreateSelector(orm, (session, allpads) => {
return allpads.filter(pad => (pad.active === true))
}));

export const playingPads = createSelector(ormSelector, activeCounts, ormCreateSelector(orm, (session, importantCounts) => {
  const trick = importantCounts.map(count=>count.pads)



return trick
}));



